<template>
        <div v-for="(item, index) in formList" :key="index" class="inputWrap" :class="{ phoneWrap: item.type === 'phone', focus: item.isFocus, error: item.isError }" @click.stop="stop">
                <div class="inputItem">
                        <p v-if="item.type === 'phone'" class="phoneArea" :class="{ active: item.isOpenList }" @click.stop="formClass.bindPhonePrefixListTransfer">
                                <img alt="" :src="item.areaFlag">+{{ item.phonePrefix }}<i class="iconfont icon-xiangxia" />
                        </p>
                        <input v-model="item.value" :type="item.inputType" class="in" :placeholder="t(item.placeholder)" @focus="formClass.bindChildFocus(index)" @blur="formClass.bindChildBlur(index)" @input="item.type==='password'?formClass.bindChildInputCheck(index):null">
                        <img v-if="item.value.length > 0" class="icon" src="@/assets/images/icons/clear.png" @click.stop="formClass.bindChildClear(index)">
                        <img v-if="item.isPassword && item.inputType === 'password'" class="icon" src="@/assets/images/icons/eyes1.png" @click.stop="formClass.bindChildChangeInputType(index, 'text')">
                        <img v-if="item.isPassword && item.inputType === 'text'" class="icon" src="@/assets/images/icons/eyes2.png" @click.stop="formClass.bindChildChangeInputType(index, 'password')">
                        <p v-if="item.type === 'validCode'" class="getCode" @click="bindStartCode">
                                {{ t(item.codeData.sendText) }}
                                <span v-if="item.codeData.isShow">({{ item.codeData.sendTime }}s)</span>
                        </p>
                        <div v-if="item.type === 'phone'" class="phoneAreaList" :class="{ active: item.isOpenList }">
                                <div class="searchWrap">
                                        <div class="search">
                                                <i class="iconfont icon-sousuo" />
                                                <input v-model="item.searchPrefixKey" class="in" @input.stop="formClass.bindSetPrefixList(index)">
                                        </div>
                                </div>
                                <div class="inner">
                                        <p v-for="(pre, idx2) in item.phonePrefixList" :key="idx2" class="item" :class="{ active: pre.isSelect }" @click.stop="formClass.bindResetPhonePrefix(index, idx2)">
                                                <span class="country">
                                                        <img alt="" :src="pre.flag">{{ pre.area }}
                                                </span>
                                                <span>+{{ pre.prefix }}</span>
                                        </p>
                                </div>
                        </div>
                </div>
                <div v-if="item.type === 'password'" class="checkPwdArea" :class="{ active: item.value.length > 0 && !item.checkPwdData.isCheckOK }">
                        <p v-for="(rule, index2) in item.checkPwdData.rules" :key="index2" class="text" :class="{ ok: rule.bol }">
                                <i v-if="rule.bol" class="iconfont icon-gouxuan" />
                                <i v-else class="iconfont icon-guanbi" />
                                <span>{{ t(rule.str) }}</span>
                        </p>
                </div>
                <p v-if="item.tips.length > 0" class="inputTips">
                        {{ t(item.tips) }}
                </p>
        </div>
        <button id="captcha-button">button</button>
        <div id="captcha-element"></div>
</template>
<script setup>
import { toRefs } from 'vue'
const props = defineProps({
        formList: {
                type: Array,
                default: [],
        },
        formClass: {
                type: Object,
                default: () => ({})
        }
})
const { formList, formClass } = toRefs(props)
const { t } = useI18n()
function bindStartCode() {
        const button = document.getElementById("captcha-button")
        const idx = formClass.value.list.findIndex(
                item => item.type === "email" || item.type === "phone",
        )
        const i = formClass.value.list.findIndex(item => item.type === "validCode")
        if (idx !== -1) {
                formClass.value.bindChildBlur(idx)
                if (formClass.value.list[idx].isOK && !formClass.value.list[i].codeData.isSend) {
                        button.click()
                }
        }
}
</script>
