                                &::-webkit-scrollbar-thumb {
                                        background: #666666;
                                }
                                
                                &::-webkit-scrollbar-track {
                                        background: transparent;
                                }
                                
                                .item {
                                        padding: 0 20px;
                                        @include dfjsbac;
